@use '../../Assets/Fonts/fonts.scss';
@use '../../Styles/variables.scss';

$max-pagewidth: 1600px;
$title-color: #3a8ab5;

.homepage-container {
  height: 100%;
  width: 100%;
  overflow-y: auto;
  overflow-x: hidden;
  background-color: variables.$background-color;
  background-color: variables.$outside-background-color;
  scroll-behavior: smooth;

  .homepage-header-container {
    background-color: rgba(255, 255, 255, 0.8);
    height: 90px;
    padding-top: 15px;

    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 0rem 0rem 0 0vw;
    cursor: pointer;

    html {
      scroll-behavior: smooth;
    }

    .title {
      font-size: 1.2rem;
      color: variables.$logo-text-color;
      font-weight: 700;
      margin-right: 0.5rem;
      padding-left: 5px;
    }

    .title-sub {
      font-weight: 100 !important;
      font-size: 1.2rem;
      color: variables.$logo-text-color;
    }

    .logo {
      height: 36px;
      padding-left: 2vw;
      margin-top: 10px;

      img {
        object-fit: cover;
        height: 36px;
        width: 34px;
      }
    }

    .country-name {
      font-size: 0.65rem;
      color: variables.$logo-text-color;
      margin-top: -5px;
      margin-left: 6px;
    }
  }

  .homepage-button-container {
    display: flex;
    justify-content: right;
    height: 90px;
    background-color: rgba(255, 255, 255, 0.8);

    button {
      margin-top: 29px;
      margin-right: 30px;
      margin-left: 30px;
      text-transform: uppercase;

      @media (max-width: 678px) {
        margin-right: 2vw;
      }
    }

    .ant-btn-primary {
      background: linear-gradient(
        to right,
        variables.$gradient-background-start,
        variables.$gradient-background-end
      );
      color: variables.$dark-text-color;
      border: none;
      transition: all ease-out 0.3s;
    }

    .ant-btn-primary:hover {
      background: linear-gradient(
        to right,
        variables.$gradient-background-end,
        variables.$gradient-background-start
      );
      transition: all ease-out 0.3s;
    }
  }

  .section {
    max-width: 1360px;
    margin: auto;
    padding: 0 2.5rem 2.5rem 2.5rem;
    width: 95%;
  }

  .hero-slider-dots {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 12px;
    margin-top: 70px;
    width: 100%;

    .hero-dot {
      width: 10px;
      height: 10px;
      border-radius: 50%;
      border: 2px solid rgba(128, 128, 128, 0.737);
      background: rgba(128, 128, 128, 0.737);
      cursor: pointer;
      transition: all 0.3s ease;
      flex-shrink: 0; // Prevent dots from shrinking

      &:hover {
        transform: scale(1.1);
      }

      &.active {
        background: white;
        border-color: white;
        transform: scale(1.2);
      }
    }

    // Responsive adjustments
    @media (max-width: variables.$md-size) {
      gap: 10px;
      margin-top: 15px;

      .hero-dot {
        width: 10px;
        height: 10px;
      }
    }

    @media (max-width: variables.$sm-size) {
      gap: 8px;
      margin-top: 12px;

      .hero-dot {
        width: 8px;
        height: 8px;
      }
    }
  }

  .homepage-img-container {
    height: 120vh;
    background-position-x: 100%;
    background-position-y: 100%;
    flex-direction: column;
    justify-content: flex-start;
    display: flex;
    flex: 1;
    text-align: left;

    .header-row {
      @media (max-width: 536px) {
        flex-flow: row;
      }
    }

    .arrow-ctn {
      display: flex;
      align-items: center;
      justify-content: center;
    }

    .arrows {
      width: 60px;
      height: 72px;
      // position: absolute;
      // left: 50%;
      // margin-left: -30px;
      // bottom: 200px;

      @media (max-width: variables.$lg-size) {
        stroke-width: 2px;
        width: 30px;
        height: 36px;
      }
    }

    .arrows path {
      stroke: #ffffff;
      fill: transparent;
      stroke-width: 2.5px;
      animation: arrow 2s infinite;
      -webkit-animation: arrow 2s infinite;

      @media (max-width: variables.$lg-size) {
        stroke-width: 2px;
      }
    }

    @keyframes arrow {
      0% {
        opacity: 0;
      }

      40% {
        opacity: 1;
      }

      80% {
        opacity: 0;
      }

      100% {
        opacity: 0;
      }
    }

    @-webkit-keyframes arrow

    /*Safari and Chrome*/ {
      0% {
        opacity: 0;
      }

      40% {
        opacity: 1;
      }

      80% {
        opacity: 0;
      }

      100% {
        opacity: 0;
      }
    }

    .arrows path.a1 {
      animation-delay: -1s;
      -webkit-animation-delay: -1s;
    }

    .arrows path.a2 {
      animation-delay: -0.5s;
      -webkit-animation-delay: -0.5s;
    }

    .arrows path.a3 {
      animation-delay: 0s;
      -webkit-animation-delay: 0s;
    }

    .text-ctn {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: flex-start; // or "center" if you want horizontal center too
      height: 75vh; // full viewport height
      margin-left: 5vw;
      font-family: variables.$secondary-font-family !important;
      font-size: 4rem;
      color: variables.$outside-background-color;
      line-height: 1.2em;
      text-transform: uppercase;
      padding-top: 80px;

      @media (max-width: 1800px) {
        font-size: 3rem;
      }

      @media (max-width: 576px) {
        font-size: 2.4rem;
        text-align: center;
        margin-left: 0px;
      }

      @media (max-width: 427px) {
        font-size: 2.2rem;
        align-items: center;
        text-align: center;
      }

      @media (max-width: 380px) {
        font-size: 2rem;
        align-items: center;
        text-align: center;
      }
    }

    .subhome {
      text-align: left;
      font-family: variables.$secondary-font-family;
      font-size: 1.5rem;
      color: variables.$outside-background-color;
      line-height: 2rem;
      text-transform: uppercase;
      margin-top: 20px;

      @media (max-width: variables.$lg-size) {
        justify-content: center;
      }

      @media (max-width: 576px) {
        text-align: center;
        font-size: 1.1rem;
      }

      @media (max-width: 427px) {
        text-align: center;
        font-size: 1.1rem;
      }
    }
  }

  .homepage-content-containerwhite {
    background-color: variables.$outside-background-color;
    height: fit-content;
    padding: 10px 60px 10px 60px;
    text-align: center;
    margin: 5px auto;
    max-width: $max-pagewidth;

    @media (max-width: variables.$lg-size) {
      height: fit-content;
      margin-top: 35px;
    }

    @media (max-width: 536px) {
      padding: 10px 0 10px 0;
    }

    .title {
      font-size: 28px;
      font-weight: 700;
      font-family: variables.$primary-font-family;
      color: variables.$title-text-color;
      color: $title-color;
      text-align: start;
      justify-content: center;
      text-transform: uppercase;
      padding: 50px 50px 0px 50px;

      @media (max-width: variables.$lg-size) {
        font-size: 24px;
        margin-top: 0rem;
        padding-top: 15px;
        color: variables.$title-text-color;
        line-height: 1.5em;
      }
    }

    .homepagebody {
      text-align: start;
      font-size: 0.875rem;
      font-weight: 400;
      font-family: variables.$primary-font-family;
      color: variables.$title-text-color;
      justify-content: center;
      margin: 20px 50px;

      @media (max-width: variables.$lg-size) {
        font-size: 0.8rem;
        margin-top: 1rem;
        color: variables.$title-text-color;
        line-height: 1.5em;
      }

      .homepagebody_text {
        font-size: 1.125rem;
        color: variables.$title-text-color;
        margin-bottom: 10px;

        &.list {
          li {
            margin: 10px 0px;
          }

          @media screen and (max-width: 767px) {
            padding-left: 20px;
          }
        }

        @media (max-width: variables.$lg-size) {
          font-size: 1rem;
          color: variables.$title-text-color;
          line-height: 1.5em;
        }

        @media (max-width: 1200px) {
          margin-top: 1rem;
        }
      }

      .homepagebody_aboutuslines {
        margin-top: 1rem;
        margin-bottom: 1rem;
        justify-content: center;
        align-items: center;
        font-size: 1.125rem;

        @media (max-width: variables.$lg-size) {
          font-size: 1rem;
          margin-top: 1rem;
          color: variables.$title-text-color;
          line-height: 1.5em;
        }

        @media (max-width: 1200px) {
          margin-top: 3rem;
        }
      }

      .homepagebody_logoaboutuslines {
        margin-top: 2rem;
        margin-bottom: 2rem;
      }

      .homepagebody_subtitle {
        font-size: 1.25rem;
        font-weight: 700;
        font-family: variables.$primary-font-family;
        color: variables.$title-text-color;
        text-align: start;
        justify-content: center;
        margin-top: 2rem;
      }
    }

    .aboutus-card-main-container {
      display: flex;
      height: 23rem;
      min-height: 20rem;
      max-width: 26rem;
      justify-content: center;
      background: linear-gradient(
        to right,
        variables.$gradient-background-start,
        variables.$gradient-background-end
      );
      border-radius: 0.625rem;
      padding: 50px 25px 25px 25px;
      margin: 10px;
      text-align: center;

      @media (max-width: 1350px) {
        height: 23rem;
        max-width: 18rem;
        padding: 50px 5px 25px 5px;
      }

      @media (max-width: 1050px) {
        max-width: 18rem;
        padding: 50px 5px 25px 5px;
      }

      @media (max-width: 986px) {
        height: 21rem;
        max-width: 18rem;
        padding: 50px 5px 25px 5px;
      }

      @media (max-width: variables.$md-size) {
        margin-bottom: -10px;
      }

      @media (max-width: variables.$sm-size) {
        padding: 40px 5px 25px 5px;
        margin: 30px 0;
      }
    }

    .aboutus-card-main-container {
      @media (max-width: variables.$xl-size) {
        margin-bottom: 0;
      }
    }

    .aboutus-card-main-container:hover {
      background: variables.$gradient-background-end;
      cursor: pointer;
    }

    .aboutus_cards-container {
      display: flex;
      flex-direction: row;
      width: 100%;
      align-items: center;
      justify-content: center;

      .aboutus_card-row {
        display: flex;
        align-items: center;
        justify-content: center;

        .aboutus_card-col {
          display: flex;
          flex-direction: row;
          justify-content: center;
          align-items: center;
        }
      }

      .aboutusIcon {
        display: flex;
        align-items: center;
        justify-content: center;
      }

      .aboutus-card-text {
        color: variables.$dark-text-color;
        font-size: 15px;
        line-height: 24px;
        // text-align: justify;
      }

      .aboutus-card-title {
        font-size: 18px;
        font-weight: 700;
        display: flex;
        color: variables.$dark-text-color;
        line-height: 2;
        text-align: center;
        margin-bottom: 10px;
        margin-top: 20px;
      }
    }

    .undplogocontainer {
      padding-top: 40px;

      .gutter-row {
        display: flex;
        align-items: center;
        justify-content: center;
      }

      .align-to-end {
        @media (max-width: variables.$md-size) {
          display: flex;
          align-items: center;
          justify-content: flex-end;
          padding-right: 20px;
        }
      }

      .align-to-start {
        @media (max-width: variables.$md-size) {
          display: flex;
          align-items: center;
          justify-content: flex-start;
          padding-left: 20px;
        }
      }

      .erbd {
        width: 15.7rem;
        // margin-top: 5px;
      }

      .undp {
        width: 4rem;
        // margin-left: 20px;
      }

      .unfccc {
        margin-top: 22px;
        width: 6.5rem;
      }

      .ieta {
        width: 14.8rem;

        @media (max-width: 876px) {
          width: 12rem;
        }

        @media (max-width: 820px) {
          width: 11rem;
        }

        @media (max-width: 750px) {
          width: 14.5rem;
        }
      }

      .esa {
        width: 15rem;
        // margin-top: -5px;

        @media (max-width: 876px) {
          width: 11rem;
        }

        @media (max-width: 820px) {
          width: 10rem;
        }

        @media (max-width: 750px) {
          width: 12.5rem;
        }
      }

      .wbank {
        width: 9rem;
        margin-top: -12px;
      }
    }
  }

  .aboutusicon {
    size: 100px;
  }

  .eligicontent {
    padding: 20px;
  }

  .homepage-image-content-container {
    background-color: variables.$outside-background-color;
    margin: 5px auto;
    max-width: $max-pagewidth;

    @media (max-width: variables.$md-size) {
      padding-left: 0;
    }

    .forest-image {
      height: 33.75rem;
      width: 58.5rem;

      @media (max-width: variables.$md-size) {
        display: none;
      }
    }

    ul {
      margin-top: 5px;
      margin-bottom: 10px;
    }

    li {
      line-height: 25px;
      margin-bottom: 10px;

      @media (max-width: variables.$sm-size) {
        line-height: 20px;
      }
    }

    .title {
      font-size: 2rem;
      font-weight: 700;
      font-family: variables.$primary-font-family;
      color: variables.$title-text-color;
      color: $title-color;
      text-align: left;
      justify-content: left;
      text-transform: uppercase;
      margin-left: 4.5rem;
      padding: 50px 50px 10px 0;

      @media (max-width: variables.$lg-size) {
        font-size: 24px;
        margin-top: 0rem;
        padding-top: 15px;
        color: variables.$title-text-color;
        line-height: 1.5em;
      }

      @media (max-width: variables.$md-size) {
        text-align: left;
        justify-content: left;
        padding: 50px 50px 10px 0;
        margin-left: 2rem;
      }
    }

    .homepagebody {
      text-align: left;
      font-size: 0.875rem;
      font-weight: 400;
      font-family: variables.$primary-font-family;
      color: variables.$title-text-color;
      justify-content: left;
      margin: 20px 10px 20px 75px;

      .homepage_accordian {
        border-radius: 5px;
        font-family: 'Inter';
        margin-bottom: 80px;

        .homepage_collapsepanel {
          border-radius: 5px !important;

          .ant-collapse-header-text {
            font-size: 18px;
            font-weight: 700;
            color: rgba(58, 53, 65, 0.8);
          }

          .collapsetext {
            font-size: 16px;
            padding: 0px 20px;
            margin-bottom: 10px;
          }
        }

        @media (max-width: variables.$lg-size) {
          .ant-collapse-header-text {
            font-size: 16px;
          }

          .collapsetext {
            font-size: 14px;

            li {
              strong {
                margin-bottom: 5px;
              }
            }
          }
        }
      }

      @media (max-width: variables.$lg-size) {
        font-size: 0.8rem;
        margin-top: 1rem;
        color: variables.$title-text-color;
        line-height: 1.5em;
      }

      @media (max-width: variables.$md-size) {
        margin: 20px 30px 20px 32px;
      }
    }
  }

  .homepage-resources-content-container {
    background-color: variables.$outside-background-color;
    height: fit-content;
    margin-top: 5px;
    height: fit-content;

    @media (max-width: variables.$lg-size) {
      height: fit-content;
    }

    ul {
      margin-top: 25px;
      margin-left: 4rem;
    }

    li {
      line-height: 40px;

      @media (max-width: variables.$sm-size) {
        line-height: 30px;
      }
    }

    img {
      display: flex;
      padding: 50px;
      justify-content: center;
      text-align: center;
      width: 28.125rem;
      height: 23.438rem;

      @media (max-width: variables.$md-size) {
        display: none;
      }
    }

    .resource-image-container {
      display: flex;
      justify-content: center;
      text-align: center;
    }

    .title {
      font-size: 2rem;
      font-weight: 700;
      font-family: variables.$primary-font-family;
      color: variables.$title-text-color;
      text-transform: uppercase;
      padding: 50px 50px 10px 0;

      @media (max-width: variables.$lg-size) {
        font-size: 2rem;
        color: variables.$title-text-color;
        line-height: 1.5em;
      }

      @media (max-width: variables.$md-size) {
        text-align: left;
        margin-left: 3rem;
        padding: 50px 50px 10px 0;
      }
    }

    .homepagebody {
      font-size: 0.875rem;
      font-weight: 400;
      font-family: variables.$primary-font-family;
      color: variables.$title-text-color;
      margin: 20px 60px 20px -80px;

      @media (max-width: variables.$lg-size) {
        font-size: 0.8rem;
        color: variables.$title-text-color;
        line-height: 1.5em;
      }

      @media (max-width: variables.$md-size) {
        margin: 20px 30px 20px -40px;
      }
    }
  }
}

.developer-resources-row {
  margin-bottom: 2rem;
  max-width: 1360px;
  margin: 20px auto 30px;
  padding: 0 20px 0 44px;
  white-space: nowrap;
  justify-content: space-between;
  gap: 15px;
}

.resource-item {
  // padding: 1rem 0;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-betweens;
  text-align: start;
}

.connects {
  color: #6c757d;
}

.resource-label {
  font-weight: bold;
  color: #6c757d;
}

.Devresources {
  margin-bottom: 1rem;
}

@media (max-width: variables.$lg-size) {
  .developer-resources-row {
    gap: 10px;
  }

  .resource-item {
    text-align: center;
    justify-content: center;
  }

  .Devresources {
    margin-bottom: 0.5rem;
  }
}

@media (max-width: variables.$md-size) {
  .developer-resources-row {
    gap: 1px;
    justify-content: center;
  }

  .resource-item {
    text-align: center;
    justify-content: center;
  }

  .Devresources {
    margin-bottom: 0.5rem;
  }
}

@media (max-width: variables.$sm-size) {
  .homepage-container {
    .homepage-header-container {
      .title {
        font-size: 0.9rem;
        line-height: 1.1rem;
        margin-bottom: 4px;
      }
    }

    .section {
      padding: 0px 1rem 1rem 1rem;
    }
  }

  .developer-resources-row {
    padding-left: 15px;
    padding-right: 15px;
  }

  .resource-item {
    padding: 0 0;
    font-size: 0.9rem;
  }
}
