}

/*flow diagram*/
/* ProcessFlow.css */
// Dashboard.scss
.process-flow-container {
  max-width: 1360px;
  margin: 10px auto 30px;
  padding: 0 40px 40px 40px;
}

.process-flow-title {
  color: #615d67;
  font-weight: 700;
  // margin-bottom: 40px;
  text-align: left;
  font-size: 1.8rem;
  margin-bottom: 1rem;
}

.process-flow-description {
  color: #615d67;
  text-align: left;
  margin-bottom: 3rem;
}

.process-flow {
  display: flex;
  flex-wrap: nowrap;
  align-items: stretch;
  justify-content: center;
  gap: 0;
  padding-bottom: 30px;
}

.step-card {
  background: white;

  border-radius: 12px;
  padding: 24px;
  text-align: center;
  width: 400px;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.card1 {
  position: relative;
  margin-bottom: 20px;
}

.card1::before {
  content: '';
  position: absolute;
  top: -17px;
  left: 6px;
  z-index: 1;
  width: 100%;
  height: 60%;
  border-top: 6px solid $accent-shade-color;
  border-left: 6px solid $accent-shade-color;
  border-right: 6px solid $accent-shade-color;
  box-sizing: border-box;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
}

.card2 {
  position: relative;
  margin-bottom: 20px;
  /* Remove the direct borders since we'll create them with pseudo-elements */
}

.card2::before {
  content: '';
  position: absolute;
  top: 50%;
  /* Start from middle (50%) */
  left: 0;
  width: 100%;
  height: 70%;
  /* From middle to bottom */
  border-bottom: 6px solid $accent-shade-color;
  border-left: 6px solid $accent-shade-color;
  border-right: 6px solid $accent-shade-color;
  box-sizing: border-box;
  border-bottom-left-radius: 12px;
  border-bottom-right-radius: 12px;
}

.card3 {
  position: relative;
  padding-left: 12px;
  /* Remove the direct borders since we'll create them with pseudo-elements */
}

.card3::before {
  content: '';
  position: absolute;
  top: -17px;
  left: -6px;
  width: 100%;
  height: 60%;
  border-top: 6px solid $accent-shade-color;
  border-left: 6px solid $accent-shade-color;
  border-right: 6px solid $accent-shade-color;
  box-sizing: border-box;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
}

.card4 {
  position: relative;
  padding-left: 8px;
  padding-bottom: 5px;
  // margin-top: 0px;
  // padding-top:18px;
  /* Remove the direct borders since we'll create them with pseudo-elements */
}

.card4::before {
  content: '';
  position: absolute;
  top: 50%;
  /* Start from middle (50%) */
  left: -12px;
  width: 100%;
  height: 60%;
  /* From middle to bottom */
  border-bottom: 6px solid $accent-shade-color;
  border-left: 6px solid $accent-shade-color;
  border-right: 6px solid $accent-shade-color;
  box-sizing: border-box;
  border-bottom-left-radius: 12px;
  border-bottom-right-radius: 12px;
}

/* Alternative approach using clip-path for cleaner L-shape */

.step-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 5px;
  margin-bottom: 16px;
  border: 3px solid $accent-color;
  border-radius: 10%;
}

.step-icon {
  filter: invert(11%) sepia(96%) saturate(2797%) hue-rotate(262deg) brightness(85%) contrast(102%);
  text-align: center;
  line-height: 1;
  display: inline-block;
}

.step-title {
  color: $accent-color;
  font-size: 16px;
  font-weight: 600;
  margin: 0 0 12px 0;
  text-align: left;
  line-height: 1.4;
  text-align: center;
}

.step-description {
  color: #64748b;
  font-size: 12px;
  line-height: 1.5;
  text-align: center;
  margin: 0;
}

