  .step-card {
    width: 100%;
    max-width: 350px;
  }

  .card1 {
    position: relative;
    padding-left: 24px;
  }

  .card1::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0px;
    width: 50%;
    height: 100%;
    border-top: 0px solid $accent-shade-color;
    border-left: 6px solid $accent-shade-color;
    border-right: 0px solid $accent-shade-color;
    border-bottom: 6px solid $accent-shade-color;
    box-sizing: border-box;
    border-top-left-radius: 0px;
    border-top-right-radius: 0px;
    border-bottom-left-radius: 12px;
  }

  .card2 {
    position: relative;
    padding-left: 24px;
    margin: 0;
    padding-top: 6px;

    /* Remove the direct borders since we'll create them with pseudo-elements */
  }

  .card2::before {
    content: '';
    position: absolute;
    /* Start from middle (50%) */
    top: -6px;
    left: 50%;
    bottom: 100%;
    width: 50%;
    height: 100%;
    /* From middle to bottom */
    border-bottom: 6px solid $accent-shade-color;
    border-left: 0px solid $accent-shade-color;
    border-right: 6px solid $accent-shade-color;
    border-top: 6px solid $accent-shade-color;
    box-sizing: border-box;
    border-bottom-right-radius: 12px;
    border-top-right-radius: 12px;
    border-bottom-left-radius: 0px;
  }

  .card3 {
    position: relative;
    padding-right: 0px;
    margin-bottom: 0px;
    /* Remove the direct borders since we'll create them with pseudo-elements */
  }

  .card3::before {
    content: '';
    position: absolute;
    top: -12px;
    left: 0px;
    width: 50%;
    height: 100%;
    border-top: 6px solid $accent-shade-color;
    border-left: 6px solid $accent-shade-color;
    border-right: 0px solid $accent-shade-color;
    border-bottom: 6px solid $accent-shade-color;
    box-sizing: border-box;
    border-top-left-radius: 12px;
    border-top-right-radius: 0px;
    border-bottom-left-radius: 12px;
  }

  .card4 {
    position: relative;
    padding-right: 24px;
    padding-left: 24px;
  }

  .card4::before {
    content: '';
    position: absolute;
    top: -18px;
    /* Start from middle (50%) */
    left: 50%;
    width: 50%;
    height: 100%;
    /* From middle to bottom */
    border-bottom: 0px solid $accent-shade-color;
    border-left: 0px solid $accent-shade-color;
    border-right: 6px solid $accent-shade-color;
    border-top: 6px solid $accent-shade-color;
    box-sizing: border-box;
    border-bottom-right-radius: 0px;
    border-top-right-radius: 12px;
  }
}

