/* Feature cards */

// Dashboard.scss - Feature Cards Component
// Dashboard.scss - Feature Cards Component
.feature-cards-containerout {
  background-image: linear-gradient(to right, $accent-color-80, $accent-color-50, $accent-color);
  margin: 0 auto;
}

.feature-cards-container {
  max-width: 1360px;
  margin: 0 auto;
  padding: 60px 20px 40px;

  .header-title {
    font-size: 1.8rem;
    margin-bottom: 1.5rem;
    margin: 0 0 1.5rem 24px;
  }
}

.feature-cards-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: auto auto;
  gap: 2rem;
  max-width: 1260px;
  margin: 0 auto;
}

.feature-cards-item {
  background: rgba(255, 255, 255, 0.1);
  /* semi-transparent white */
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  /* optional border for depth */
  border-radius: 16px;
  /* optional for round edges */
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  /* optional for elevation */

  border-radius: 20px;
  padding: 2.5rem;
  color: white;
  transition: all 0.3s ease;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  width: 100%;

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(
      135deg,
      rgba(255, 255, 255, 0.1) 0%,
      rgba(255, 255, 255, 0.05) 100%
    );
    opacity: 0;
    transition: opacity 0.3s ease;
  }

  &.feature-cards-large {
    grid-column: span 1;
    min-height: 240px;

    // First row positioning
    &:nth-child(2) {
      grid-column: 2 / 4;
      grid-row: 1;
    }

    &:nth-child(1) {
      grid-column: 1 / 2;
      grid-row: 1;
    }
  }

  &.feature-cards-small {
    min-height: 200px;
    grid-row: 2;

    // Second row positioning - 3 equal cards
    &:nth-child(3) {
      grid-column: 1;
    }

    &:nth-child(4) {
      grid-column: 2;
    }

    &:nth-child(5) {
      grid-column: 3;
    }
  }
}

.feature-cards-icon {
  width: 50px;
  height: 50px;
  // background-color: #FFFFFF;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
  font-weight: bold;
  margin-bottom: 1.5rem;
  transition: transform 0.3s ease;
  border: 2px solid #ffffff;
}

.feature-cards-heading {
  font-size: 1.25rem;
  font-weight: bold;
  margin-bottom: 1rem;
  line-height: 1.3;
  color: white;
}

.feature-cards-text {
  font-size: 0.95rem;
  line-height: 1.5;
  opacity: 0.9;
  margin: 0;
}

// Hover Effects - Fixed to prevent blur on gaps
.feature-cards-item:hover ~ .feature-cards-item,
.feature-cards-item:has(~ .feature-cards-item:hover) {
  transform: scale(0.92);
  opacity: 0.7;
  filter: blur(1px);
}

// Alternative approach using JavaScript-like selector logic
.feature-cards-grid {
  &:has(.feature-cards-item:hover) .feature-cards-item:not(:hover) {
    transform: scale(0.92);
    opacity: 0.7;
    filter: blur(1px);
  }
}

.feature-cards-item:hover {
  transform: scale(1.05);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3);
  z-index: 10;

  &::before {
    opacity: 1;
  }

  .feature-cards-icon {
    transform: scale(1.1);
    background: $accent-color;
    /* transparent teal glass */
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);
  }

  .feature-cards-heading {
    color: #ffffff;
  }
}

// Responsive Design
@media (max-width: 768px) {
  .feature-cards-grid {
    grid-template-columns: 1fr;
    grid-template-rows: repeat(5, auto);
    max-width: 400px;
  }

  .feature-cards-item {
    min-height: 180px;

    &.feature-cards-large,
    &.feature-cards-small {
      grid-column: 1 !important;
      grid-row: auto !important;
    }

    &:nth-child(1) {
      grid-row: 1;
    }

    &:nth-child(2) {
      grid-row: 2;
    }

    &:nth-child(3) {
      grid-row: 3;
    }

    &:nth-child(4) {
      grid-row: 4;
    }

    &:nth-child(5) {
      grid-row: 5;
    }
  }

  .feature-cards-container .feature-cards-title {
    font-size: 1.5rem;
  }
}

