.status-chip {
  .pending-chip {
    align-content: center;
    height: 28px;
    width: 90px;
    border-width: 0;
    background: #e8e8e8;
    color: #918e94;
    overflow-x: auto;

    &::-webkit-scrollbar {
      display: none;
    }

    &::-webkit-scrollbar-track {
      display: none;
    }

    &::-webkit-scrollbar-thumb {
      display: none;
    }
  }

  .validated-chip {
    align-content: center;
    height: 28px;
    width: 90px;
    border-width: 0;
    background-color: rgba(#80ff00, 0.12);
    color: #519e4f;
    overflow-x: auto;

    &::-webkit-scrollbar {
      display: none;
    }

    &::-webkit-scrollbar-track {
      display: none;
    }

    &::-webkit-scrollbar-thumb {
      display: none;
    }
  }
}
